import { Analytics } from 'analytics';
import googleTagManager from '@analytics/google-tag-manager';
import googleAnalytics from '@analytics/google-analytics';
let analytics;
export function setupAnalytics(adapter, adapterConfig, app) {
    //   Config with incoming values.
    // console.log('adapter config > ', adapterConfig);
    // console.log('Plugins  > ', plugins);
    // Do event names need to be passed in?
    // eg. Category, button click, thumbs up/down, close...
    // const adapterEventName = 'Feedback Loaded';
    // analytics.track(adapterEventName, {
    //   category: `Feedback ${currentPath}`,
    // });
    const plugins = [];
    if (adapter === 'gtm') {
        plugins.push(googleTagManager(adapterConfig));
    }
    else if (adapter === 'ga') {
        plugins.push(googleAnalytics(adapterConfig));
    }
    analytics = Analytics({
        debug: true,
        app,
        plugins,
    });
    // For testing
    // Fire function when 'track' calls happen
    // analytics.on('track', ({ payload }) => {
    //   console.log('track call just happened. Payload > ', payload);
    // });
}
export function trackEvent(currentPath, eventName, eventLabel) {
    // track a custom event.
    // track pageview
    // analytics.page()
    // payload
    //
    // eventName - Prefex + current step or event
    // currentPath - url for page
    // eventAnswer - or value to track.
    // WIP Values don't transfer exactly from Google.
    // trackcustom event
    const adapterEventName = eventName;
    analytics.track(adapterEventName, {
        category: `Feedback ${currentPath}`,
        label: eventLabel,
    });
}
